parturient montes, nascetur ridiculus mus. Morbi consequat auctor turpis, vitae dictum augue efficitur vitae. Vestibulum a risus ipsum. Quisque nec lacus dolor. Quisque ornare tempor orci id rutrum.</p>\n    <template #modal-footer>\n        <b-button variant=\"default\" data-dismiss=\"modal\" @click=\"$bvModal.hide('standardModal')\"><i class=\"flaticon-cancel-12\"></i> Discard</b-button>\n        <b-button variant=\"primary\">Save</b-button>\n    </template>\n</b-modal>\n<!-- Tabs Modal -->\n<b-modal id=\"tabsModal\" title=\"Modal title\" modal-class=\"text-center\">\n    <b-tabs nav-class=\"mb-3\">\n        <b-tab title=\"Home\" active>\n            <p class=\"modal-text\">Vivamus vitae hendrerit neque. Orci varius natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Morbi consequat auctor turpis, vitae dictum augue efficitur vitae. Vestibulum a risus ipsum. Quisque nec lacus dolor. Quisque ornare tempor orci id rutrum.</p>\n        </b-tab>\n        <b-tab title=\"Profile\">\n            <p class=\"modal-text\">Fusce ac fringilla ex. Sed ligula ipsum, fringilla ut orci nec, suscipit commodo felis. Sed imperdiet eros dignissim, vehicula erat vel, rutrum lorem. In porttitor id ante nec laoreet. Etiam quis sapien ac nunc ullamcorper elementum. Fusce ullamcorper ante convallis nisl eleifend, sit amet dapibus urna eleifend.</p>\n        </b-tab>\n        <b-tab title=\"Contact\">\n            <p class=\"modal-text\">Pellentesque semper tortor id ligula ultrices suscipit. Donec viverra vulputate lectus non consectetur. Donec ac interdum lacus. Donec euismod nisi at justo molestie elementum. Vivamus vitae hendrerit neque. Orci varius natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus.</p>\n        </b-tab>\n    </b-tabs>\n    <template #modal-footer>\n        <b-button variant=\"default\" data-dismiss=\"modal\" @click=\"$bvModal.hide('tabsModal')\"><i class=\"flaticon-cancel-12\"></i> Discard</b-button>\n        <b-button variant=\"primary\">Save</b-button>\n    </template>\n</b-modal>\n<!-- Profile Modal -->\n<b-modal id=\"profileModal\" modal-class=\"text-center profile-modal\" header-class=\"justify-content-center p-0 border-0\" footer-class=\"justify-content-center mb-4\" size=\"sm\">\n    <div class=\"modal-profile mt-4 mb-5\">\n        <img alt=\"avatar\" src=\"@/assets/images/profile-2.jpeg\" class=\"rounded-circle\">\n    </div>\n    <p class=\"mt-2\">Click on view to access your profile.</p>\n    <template #modal-footer>\n        <b-button variant=\"default\">View</b-button>\n    </template>\n</b-modal>\n<!-- Login Modal -->\n<b-modal id=\"loginModal\" title=\"Login\" title-tag=\"h4\" modal-class=\"login-modal\" footer-class=\"justify-content-center\">\n    <form class=\"mt-0\">\n        <div class=\"form-group\">\n            <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"24\" height=\"24\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-user\"><path d=\"M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2\"></path><circle cx=\"12\" cy=\"7\" r=\"4\"></circle></svg>\n            <b-form-input type=\"email\" class=\"mb-2\" placeholder=\"Email\"></b-form-input>\n        </div>\n        <div class=\"form-group\">\n            <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"24\" height=\"24\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-lock\"><rect x=\"3\" y=\"11\" width=\"18\" height=\"11\" rx=\"2\" ry=\"2\"></rect><path d=\"M7 11V7a5 5 0 0 1 10 0v4\"></path></svg>\n            <b-form-input type=\"password\" class=\"mb-4\" placeholder=\"Password\"></b-form-input>\n        </div>\n        <b-button variant=\"primary\" block class=\"mt-2 mb-2\" @click=\"$bvModal.hide('loginModal')\">Login</b-button>\n    </form>\n\n    <div class=\"division\">\n        <span>OR</span>\n    </div>\n\n    <div class=\"social\">\n        <a href=\"javascript:void(0);\" class=\"btn social-fb mx-1\"><svg xmlns=\"http://www.w3.org/2000/svg\" width=\"24\" height=\"24\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-facebook\"><path d=\"M18 2h-3a5 5 0 0 0-5 5v3H7v4h3v8h4v-8h3l1-4h-4V7a1 1 0 0 1 1-1h3z\"></path></svg> <span class=\"brand-name\">Facebook</span></a>\n        <a href=\"javascript:void(0);\" class=\"btn social-github\"><svg xmlns=\"http://www.w3.org/2000/svg\" width=\"24\" height=\"24\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-github\"><path d=\"M9 19c-5 1.5-5-2.5-7-3m14 6v-3.87a3.37 3.37 0 0 0-.94-2.61c3.14-.35 6.44-1.54 6.44-7A5.44 5.44 0 0 0 20 4.77 5.07 5.07 0 0 0 19.91 1S18.73.65 16 2.48a13.38 13.38 0 0 0-7 0C6.27.65 5.09 1 5.09 1A5.07 5.07 0 0 0 5 4.77a5.44 5.44 0 0 0-1.5 3.78c0 5.42 3.3 6.61 6.44 7A3.37 3.37 0 0 0 9 18.13V22\"></path></svg> <span class=\"brand-name\">Github</span></a>\n    </div>\n\n    <template #modal-footer>\n        <div class=\"forgot login-footer\">\n            <span>Looking to <a href=\"javascript:void(0);\">create an account</a>?</span>\n        </div>\n    </template>\n</b-modal>\n<!-- Register Modal -->\n<b-modal id=\"registerModal\" title=\"Register\" title-tag=\"h4\" modal-class=\"register-modal\" footer-class=\"justify-content-center\">\n    <form class=\"mt-0\">\n        <div class=\"form-group\">\n            <svg xmlns=\"http://www.w3.org/2000/svg\" width=\"24\" height=\"24\" viewBox=\"0 0 24 24\" fill=\"none\" stroke=\"currentColor\" stroke-width=\"2\" stroke-linecap=\"round\" stroke-linejoin=\"round\" class=\"feather feather-user\"><path d=\"M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2\"></path><circle cx=\"12\" cy=\"7\" r=\"4\"></circle></svg>\n            <b-form-input type=\"text\" class=\"